
.headbreadcrumb{
  background-color: $mainpage-color-white;
  padding: 25px 0 15px;

  > .container{
    position: relative;
    padding-bottom: 5px;

    &:before{
      content: "";
      background-color: transparentize($mainpage-color-light-stale-gray, 0.7);
      width: 100%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &:after{
      content: "";
      background-color: $mainpage-color-blue;
      width: 60px;
      height: 4px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  .breadcrumb{
    color: $mainpage-color-title-blue;
    background-color: transparent;
    margin: 0;
    padding: 0;

    &-item {
      position: relative;

      + .breadcrumb-item{
        padding-left: 24px;

        &:before{
          content: "";
          position:absolute;
          width: 9px;
          height: 9px;
          top: calc(50% - 5px);
          left: 4px;
          padding: 0;
          border-top: 2px solid $mainpage-color-blue;
          border-right: 2px solid $mainpage-color-blue;
          @include rotate(45);
        }
      }

      &.active {
        color: $mainpage-color-blue;
      }
    }
  }

  &-title{
    justify-content: flex-start;
    font-size: 25px;
    font-weight: normal;

    .breadcrumb-item{
      + .breadcrumb-item{
        &:before{
          width: 12px;
          height: 12px !important;
          top: calc(50% - 6px);
          left: 4px;
        }
      }
    }
  }

  &-breadcrumb{
    justify-content: flex-end;
    font-size: 17px;
    font-weight: normal;
  }
}